import {
  Box,
  Divider,
  Grid,
  GridItem,
  SimpleGrid,
  Text,
} from '@chakra-ui/react'
// stylesheets
import '../styles/Box.scss'

function Home(props: {
                questionNum: number,  // 
                answeredNum: number,  // 
                progress: number,     // 
                adviceList: { progress: number, text: string }[], // 
                updateProgress: (p: number) => void  // 
              }) {
  const maxProgress = props.adviceList.length - 1;
  let p = props.progress;
  if (props.progress > maxProgress) {
    p = maxProgress;
  }

  return (
    <>
      <SimpleGrid columns={1} spacing={1}>
      {/*  */}
      <Box className='panel'>
        <Text fontSize='5xl'></Text>

        <Divider />

        <Grid templateColumns='repeat(2, 1fr)' gap={2} className='box'>
          <GridItem w='100%'></GridItem>
          <GridItem w='100%' bg='blue.700'>
            <Text fontSize='2xl' className='box-header'></Text>
            <Text fontSize='4xl'>{props.answeredNum} / {props.questionNum}</Text>
          </GridItem>
        </Grid>
      </Box>
      {/*  */}
      <Box className='text-window'>
        {props.adviceList[p].text}
      </Box>
      </SimpleGrid>
    </>
  )
}

export default Home;